<template>
  <div id="AboutUs" class="color999" v-if="data">
    <header-nav :title="msg"></header-nav>
    <div class="top text-center">
      <div class="inline-block Logo">
        <img :src="data.about.site_logo.value" @click="showDebugFn" alt="">
      </div>
      <div class="name">
        {{data.about.site_name.value}}
      </div>
      <div class="versionText">
        {{$config.version}}
      </div>
      <div class="txt border-1px">
        {{data.about.site_slogan.value}}
      </div>
    </div>
    <ul class="f28">
      <li  class="clear cbgzh border-1px" @click="openQQ" :data-clipboard-text="data.about.site_qqqun.value">
        <div class="fl font-bColor">QQ群</div>
        <div class="fr icon_right">
          <img  src="../../assets/img/my_right.png" alt="">
        </div>
        <div class="fr mR20">
          {{data.about.site_qqqun.value}}
        </div>
      </li>
      <!-- <li  class="clear cbgzh" @click="wechatFn" :data-clipboard-text="data.about.site_wechat.value">
        <div class="fl font-bColor" >公众号</div>
        <div class="fr icon_right">
          <img  src="../../assets/img/my_right.png" alt="">
        </div>
        <div class="fr mR20">
          {{data.about.site_wechat.value}}
        </div>
      </li> -->
      <li  class="clear cbgzh" @click="wechatFn" >
        <div class="fl font-bColor" >掘金宝官网</div>
        <div class="fr icon_right">
          <img  src="../../assets/img/my_right.png" alt="">
        </div>
        <div class="fr mR20" @click="openOF">
          www.juejinchain.com
        </div>
      </li>
      <!-- <li v-if="$config.packageTime" class="clear cbgzh" @click="wechatFn" >
        <div class="fl font-bColor" >发布时间</div>
        <div class="fr icon_right">
          <img  src="../../assets/img/my_right.png" alt="">
        </div>
        <div class="fr mR20" @click="openOF">
         {{$utils.timestampToStr($config.packageTime.getTime(), 'Y/M/D h:m:s')}}
        </div>
      </li> -->
      <!-- <li  class="clear cbgzh" @click="openDown"  v-if="showSHO" >
        <div class="fl font-bColor" >下载掘金宝</div>
        <div class="fr icon_right">
          <img  src="../../assets/img/my_right.png" alt="">
        </div>
      </li> -->
      <li  class="clear mT30 border-1px">
        <div class="fl font-bColor">版本更新说明</div>
      </li>
      <li  class="clear pT30 pB30 version"
      v-for="(item,index) in data.list"
      :key="item.new_version_num"
      :class="{'border-1px':index!=(data.list.length-1)}">
        <div class="font-bColor">{{item.new_version_num}}</div>
        <div class="f26" v-html="item.content"></div>
      </li>
    </ul>
    <div class="checkoutUpdate" @click="getUpdate">
      {{$config.source_style >= 6 ? '检查更新' : '下载掘金宝APP'}}
    </div>
    {{data.about.site_qqqun_url.value}}
    <prompt-box
      :dropBol="isToQQ"
      @leftBtn="isToQQ=false"
      @rightBtn="confirmBtnQQ(data.about.site_qqqun_url.value)"
      rightTxt="确定"
      contentTxt="即将打开QQ，是否继续？"
    >
    </prompt-box>
    <prompt-box
      :dropBol="official"
      @leftBtn="official=false"
      @rightBtn="confirmBtn('http://www.juejinchain.com/')"
      rightTxt="确定"
      contentTxt="即将跳转到官网，是否继续？"
    >
    </prompt-box>
  </div>
</template>

<script>
import HeaderNav from '../common/header-nav'
import Clipboard from 'clipboard'
import PromptBox from '@/components/common/prompt-box'
export default {
  name: 'AboutUs',
  data () {
    return {
      msg: '关于我们',
      data: null,     // 关于我们的数据
      showSHO: false,
      isToQQ: false,
      official: false,
      debugCount: 0
    }
  },
  methods: {
    // 打开qq
    openQQ () {
      if (this.$config.source_style >= 6) {
        this.isToQQ = true
      } else {
        if (this.$utils.isXiaoMi()) {
          if (Clipboard.isSupported()) {
            let clb = new Clipboard('.cbgzh')
            clb.on('success',  (e) => {
              this.$toast({
                message: '已复制qq群号，请用QQ搜索加入',
                position: 'middle',
                duration: 1500
              })
              clb.destroy()
            })
            clb.on('error',  (e) => {
              this.$toast({
                message: e.text + '复制失败',
                position: 'middle',
                duration: 1500
              })
              clb.destroy()
            })
          }
        } else {
          this.isToQQ = true
        }
      }
    },
    confirmBtn (url) {
      if (this.$config.source_style >= 6) {
        window.plus.runtime.openURL(url)
      } else {
        window.open(url, '_self')
      }
      this.isToQQ = false
      this.official = false
    },
    // 打开QQ
    confirmBtnQQ (url) {
      if (this.$config.source_style >= 6) {
        this.$utils.plusReady(plus => {
          plus.webview.open('./openApp.html')
        })
      } else {
        console.log(url)
        // 浏览器需判断是否是小米
        window.open(url, '_self')
      }
      this.isToQQ = false
      this.official = false
    },
    // 获取版本更新
    getUpdate () {
      if (this.$config.source_style >= 6) {
        this.$api.get('CHECK_UPRADE', {vsn: this.$config.version}, res => {
          if (res.data.is_upgrade === 1) {
            this.$store.commit('setShowShare', true)
            this.$store.commit('set_shareType', 8)
          } else {
            this.$toast({
              message: '暂无版本更新'
            })
          }
        })
      } else {
        this.$utils.openDownUrl()
      }
    },
    // 复制公众号
    wechatFn () {
      if (this.$utils.isWx()) {
        window.location.href = 'http://mp.weixin.qq.com/mp/getmasssendmsg?__biz=MzU2NjY5NTU1NQ==#wechat_webview_type=1&wechat_redirect'
      } else {
        if (Clipboard.isSupported()) {
          let clb = new Clipboard('.cbgzh')
          clb.on('success',  (e) => {
            this.$toast({
              message: '已复制,请用微信搜索',
              position: 'middle',
              duration: 1500
            })
            clb.destroy()
          })
          clb.on('error',  (e) => {
            this.$toast({
              message: e.text + '复制失败',
              position: 'middle',
              duration: 1500
            })
            clb.destroy()
          })
        }
      }
    },
    // 获取关于我们的数据
    getAbout () {
      this.$api.get('ABOUT', {}, (res) => {
        this.data = res.data
        // res.data.time = res.time
        // this.$utils.cache('about', JSON.stringify(res.data))
      })
    },
    // 判断时间是否过12点，过了就重新拿数据，没过就拿本地存储
    judgmentTime () {
      if (!this.$utils.cache('about')) {
        this.getAbout()
        return
      }
      const oldTime = new Date(JSON.parse(this.$utils.cache('about')).time * 1000)
      const newTime = new Date()
      if (oldTime.getDate() !== newTime.getDate()) {
        this.getAbout()
      } else {
        this.data = JSON.parse(this.$utils.cache('about'))
      }
    },
    openOF () {
      this.official = true
    },
    openDown () {
      if (this.$utils.isAndroid) {
        window.open(this.$config.androidDownloadUrl, '_self')
      } else {
        window.open(this.$config.iOSDownloadUrl, '_self')
      }
    },
    // 启动调试模式
    showDebugFn () {
      this.debugCount++
      if (this.debugCount >= 3) {
        if (!this.$store.state.common.isDebug) {
          this.$store.commit('common_set_debug', true)
        } else {
          this.$store.commit('common_set_debug', false)
        }
        this.debugCount = 0
      }
    }
  },
  created () {
    this.judgmentTime()
    if (this.$config.source_style === 7) {
      this.showSHO = false
    } else if (this.$config.source_style === 6) {
      this.showSHO = false
    } else if (this.$config.source_style < 6) {
      this.showSHO = true
    }
  },
  components: {
    HeaderNav,
    PromptBox
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" type="text/css">
#AboutUs{
  background: #F0F4F5;
  ul{
    margin-bottom: 100px;
  }
  .top,li{
    background: #fff;
  }
  .versionText {
    margin: 10px 0;
    color: #333
  }
  .version{
    line-height: 45px;
    padding: 32px 32px 50px;
  }
  li{
    line-height: 100px;
    padding: 0 32px;
    .fr{
      color: #999;
    }
  }
  .icon_right{
    width: 16px;
    height: 26px;
    img{
      width: 100%;
      height: 100%;
    }
  }
  .checkoutUpdate{
    position: fixed;
    width: 100%;
    background: #FFFFFF;
    display: flex;
    align-items: center;
    justify-content: center;
    bottom: 0;
    box-shadow: 0 -1px 0 0 #EEEEEE;
    height: 100px;
    color: #F37F32;
  }
  .txt{
    color: #666;
    padding-bottom: 30px;
    line-height: 45px;
  }
  .name{
    color: #FFAA00;
    margin: 16px 0px 0;
  }
  .Logo{
    width: 130px;
    height: 130px;
    padding-top: 85px;
    img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }
}
.font-bColor{
  color: #333;
  font-size: 32px;
}
</style>



